export function getHM(date) {
  var time = new Date(date);

  var hours = time.getHours();
  var minutes = time.getMinutes();

  if (minutes.toString().length === 1) {
    minutes = "0" + minutes;
  }
  if (hours.toString().length === 1) {
    hours = "0" + hours;
  }

  return hours + ":" + minutes;
}

export function timeAt(date) {
  var time = new Date(date);

  var day_diff = new Date().getDate() - time.getDate(),
    month_diff = new Date().getMonth() - time.getMonth(),
    year_diff = new Date().getFullYear() - time.getFullYear();
  var year = time.getFullYear(),
    month = time.getMonth() + 1,
    day = time.getDate();

  if (year_diff > 0)
    return (
      year.toString() +
      "." +
      (month < 10 ? "0" + month.toString() : month.toString()) +
      "." +
      (day < 10 ? "0" + day.toString() : day.toString())
    );

  var arr = [
    "–Ø–Ω–≤.",
    "–§–µ–≤.",
    "–ú–∞—Ä.",
    "–ê–ø—Ä.",
    "–ú–∞–π",
    "–ò—é–Ω.",
    "–ò—é–ª.",
    "–ê–≤–≥.",
    "–°–µ–Ω.",
    "–û–∫—Ç.",
    "–ù–æ—è.",
    "–î–µ–∫.",
  ];

  var r =
    (day_diff === 0 && month_diff === 0 && "–°–µ–≥–æ–¥–Ω—è") ||
    (day_diff === 1 && month_diff === 0 && "–í—á–µ—Ä–∞") ||
    (year_diff === 0 && day + " " + arr[month - 1]);
  return r;
}

export function LastMessageDate(timeR) {
  var time = new Date(timeR);

  var hours = time.getHours();
  var minutes = time.getMinutes();

  if (minutes.toString().length === 1) {
    minutes = "0" + minutes;
  }
  if (hours.toString().length === 1) {
    hours = "0" + hours;
  }

  var day_diff = new Date().getDate() - time.getDate(),
    month_diff = new Date().getMonth() - time.getMonth(),
    year_diff = new Date().getFullYear() - time.getFullYear();
  var year = time.getFullYear(),
    month = time.getMonth() + 1,
    day = time.getDate();

  if (year_diff > 0)
    return (
      (day < 10 ? "0" + day.toString() : day.toString()) +
      "." +
      (month < 10 ? "0" + month.toString() : month.toString()) +
      "." +
      year.toString()
    );

  var arr = [
    "–Ø–Ω–≤.",
    "–§–µ–≤.",
    "–ú–∞—Ä.",
    "–ê–ø—Ä.",
    "–ú–∞–π",
    "–ò—é–Ω.",
    "–ò—é–ª.",
    "–ê–≤–≥.",
    "–°–µ–Ω.",
    "–û–∫—Ç.",
    "–ù–æ—è.",
    "–î–µ–∫.",
  ];

  var r =
    (day_diff === 0 && month_diff === 0 && hours + ":" + minutes) ||
    (day_diff === 1 && month_diff === 0 && "–í—á–µ—Ä–∞") ||
    (year_diff === 0 && day + " " + arr[month - 1]);

  return r;
}

export function OnlineDate(timeR) {
  var time = new Date(timeR);

  var hours = time.getHours();
  var minutes = time.getMinutes();

  if (minutes.toString().length === 1) {
    minutes = "0" + minutes;
  }
  if (hours.toString().length === 1) {
    hours = "0" + hours;
  }

  var diff = (new Date().getTime() - time.getTime()) / 1000,
    day_diff = new Date().getDay() - time.getDay();
  var month_diff = new Date().getMonth() + 1 - (time.getMonth() + 1);
  var year = time.getFullYear(),
    month = time.getMonth() + 1,
    day = time.getDate();

  if (isNaN(day_diff) || day_diff < 0 || month_diff >= 1)
    return (
      year.toString() +
      "." +
      (month < 10 ? "0" + month.toString() : month.toString()) +
      "." +
      (day < 10
        ? "0" + day.toString() + " in " + hours + ":" + minutes
        : day.toString() + " in " + hours + ":" + minutes)
    );

  var r =
    (day_diff === 0 &&
      ((diff < 60 && "–¢–æ–ª—å–∫–æ —á—Ç–æ") ||
        (diff < 120 && "1 –º–∏–Ω—É—Ç—É –Ω–∞–∑–∞–¥") ||
        (diff < 3600 && Math.floor(diff / 60) + "–º–∏–Ω—É—Ç. –Ω–∞–∑–∞–¥") ||
        (diff < 7200 && "1 —á–∞—Å –Ω–∞–∑–∞–¥") ||
        (diff < 86400 && Math.floor(diff / 3600) + "—á–∞—Å–æ–≤ –Ω–∞–∑–∞–¥"))) ||
    (day_diff === 1 && "–í—á–µ—Ä–∞ –≤" + hours + ":" + minutes) ||
    (day_diff < 7 && day_diff + "–¥–Ω. –Ω–∞–∑–∞–¥ –≤" + hours + ":" + minutes) ||
    (day_diff < 31 &&
      Math.ceil(day_diff / 7) + "–Ω–µ–¥–µ–ª. –≤" + hours + ":" + minutes);
  return r;
}
export function prettyDate(num) {
  /**
   * –û–ø—Ä–µ–¥–µ–ª—è–µ—Ç –ø—Ä–∞–≤–∏–ª—å–Ω–æ–µ —Å —Ç–æ—á–∫–∏ –∑—Ä–µ–Ω–∏—è —Ä—É—Å—Å–∫–æ–≥–æ —è–∑—ã–∫–∞ –æ–∫–æ–Ω—á–∞–Ω–∏–µ –º–µ—Å—è—Ü–∞
   * @param int $num
   * @param array $postfixes
   * @return string
   */
  function postfix(num, postfixes) {
    //–î–µ–ª–∏–º —á–∏—Å–ª–æ –±–µ–∑ –æ—Å—Ç–∞—Ç–∫–∞ –Ω–∞ 100
    num = num % 100;

    //–ï—Å–ª–∏ –±–æ–ª—å—à–µ 19, –¥–µ–ª–∏–º –µ–≥–æ –±–µ–∑ –æ—Å—Ç–∞—Ç–∫–∞ –µ—â—ë —Ä–∞–∑, —É–∂–µ –Ω–∞ 10
    if (num > 19) {
      num = num % 10;
    }

    //–í –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ç–æ–≥–æ, –∫–∞–∫–∏–µ —á–∏—Å–ª–∞ –æ—Å—Ç–∞–ª–∏—Å—å, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏—è
    switch (num) {
      case 1:
        return postfixes[0];

      case 2:
      case 3:
      case 4:
        return postfixes[1];

      default:
        return postfixes[2];
    }
  }

  //–û–ø—Ä–µ–¥–µ–ª—è–µ–º –º–∞—Å—Å–∏–≤—ã —Å –≥–æ–¥–∞–º–∏ –∏ –º–µ—Å—è—Ü–∞–º–∏
  var yearsPostfixes = ["–≥–æ–¥", "–≥–æ–¥–∞", "–ª–µ—Ç"],
    monthesPostfixes = ["–º–µ—Å—è—Ü", "–º–µ—Å—è—Ü–∞", "–º–µ—Å—è—Ü–µ–≤"];

  //–î–µ–ª–∏–º –Ω–∞—á–∞–ª—å–Ω–æ–µ —á–∏—Å–ª–æ –±–µ–∑ –æ—Å—Ç–∞—Ç–∫–∞ –Ω–∞ 12, –ø–æ–ª—É—á–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–µ—Å—è—Ü–µ–≤
  var monthes = num % 12,
    //–û—Ç–Ω–∏–º–∞–µ–º –∏–∑ –Ω–∞—á–∞–ª—å–Ω–æ–≥–æ —á–∏—Å–ª–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–µ—Å—è—Ü–µ–≤ –∏ –¥–µ–ª–∏–º —ç—Ç–æ –Ω–∞ 12,
    //–ø–æ–ª—É—á–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ª–µ—Ç
    years = (num - monthes) / 12;

  //–í–æ–∑–≤—Ä–∞—â–∞–µ–º —Ä–µ–∑—É–ª—å—Ç–∞—Ç postfix() –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∑–Ω–∞—á–µ–Ω–∏–π $years –∏ $monthes
  if (years === 0) {
    return monthes + " " + postfix(monthes, monthesPostfixes);
  }

  if (monthes === 0) {
    return years + " " + postfix(years, yearsPostfixes);
  }

  return (
    years +
    " " +
    postfix(years, yearsPostfixes) +
    " " +
    monthes +
    " " +
    postfix(monthes, monthesPostfixes)
  );
}
